.about {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 32px;
  justify-items: center;
  &__img {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  &__content {
    max-width: 600px;
    width: 100%;
    h3 {
      font-size: 30px;
      font-weight: 600;
      padding-top: 10px;
      margin-bottom: 20px;
    }
    ul {
      list-style-type: disc;
      list-style: disc;
      padding-left: 32px;
    }
    p, ul li {
      font-size: 20px;
      line-height: 140%;
      letter-spacing: .1px;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      a {
        font-size: inherit;
        text-decoration: underline;
      }
    }
  }
}